.about-me {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 131px;
  padding-bottom: 137px;

  &__container {
    width: 1140px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    color: #000000;
  }

  &__content {
    display: flex;
    margin-top: 44px;

    &-img {
      margin-left: 36px;
      margin-top: 7px;
    }

    &-info {
      margin-left: 65px;

      &-title {
        color: #000000;
        margin-bottom: 9px;
      }

      &-text {
        color: #4c4b4b;
        margin-top: 6px;

        &:first-child {
          margin-top: 0;
        }
      }

      &-link {
        display: block;
        margin-top: 14px;

        &:active {
          -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
          -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
          outline: none !important;
        }
      }
    }
  }
}

@media (max-width: 1170px) {
  .about-me {
    padding-top: 130px;
    padding-bottom: 137px;

    &__container {
      width: 929px;
    }

    &__title {
      letter-spacing: -0.5px;
    }

    &__content {

      &-img {
        margin-top: -17px;
        margin-left: 0;
      }

      &-info {
        margin-left: 30px;

        &-title {
          margin-bottom: 9px;
        }

        &-text {
          margin-top: 6px;
        }

        &-link {
          margin-top: 22px;
        }
      }
    }
  }
}

@media (max-width: 960px) {
  .about-me {
    padding-top: 91px;
    padding-bottom: 99px;

    &__container {
      width: 721px;
    }

    &__content {
      margin-top: 42px;

      &-img {
        margin-top: -29px;
        margin-left: -34px;
        transform: scale(0.76);
      }

      &-info {
        margin-left: -5px;

        &-title {
          margin-bottom: 14px;
        }

        &-text {
        }

        &-link {
        }
      }
    }
  }
}

@media (max-width: 755px) {
  .about-me {
    padding-top: 54px;
    padding-bottom: 57px;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__content {
      margin-top: 20px;
      flex-direction: column;

      &-img {
        display: flex;
        justify-content: center;
        margin-left: 0;
        margin-top: -25px;
      }

      &-info {
        margin-left: 0;
        margin-top: -12px;

        &-title {
          margin-bottom: 15px;
          padding: 0 5px;
          text-align: center;
        }

        &-text {
          text-align: center;
        }

        &-link {
          margin-top: 17px;
          text-align: center;
        }
      }
    }
  }
}
