.link {
  color: #000000;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;

  &:hover {
    color: #ef6c00;
  }
}

.bold-link {
  color: #ef6c00;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
  transition: 0.2s color ease-in-out;

  &:hover {
    color: #fc831f;
  }
}